.navbar-container{
    display: flex;
    flex-direction: row;
    height: 10vh;
    justify-content: space-between;
    padding: 16px 16px;
    background-color: #4259dd;
    color: white;
}

.navbar-left{
    width: 40%;
}
.navbar-right{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 60%;
}